.App-nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.App-nav-item {
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-inline-flexbox;
    display: inline-flex;
}

.App-nav-item:hover, .App-nav-item.active {
    background-color: #fff;
}

.App-nav-item a {
    padding: 20px 40px;
    color: #fff;
    font-size: 18px;
    text-decoration: none;
}

.App-nav-item:hover a, .App-nav-item.active a{
    color: #000;
    font-weight: bold;
}



