p {
  margin-bottom: 10px !important;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  padding: 0 40px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
}

.App-logo {
  height: 58px;
  margin-right: 40px;
}

.timeline {
  padding-bottom: 50px;
  background-color: #000;
}

.timeline ul li {
  position: relative;
  width: 6px;
  margin: 0 auto;
  padding-top: 50px;
  background: #fff;
}

.timeline ul li::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #fedfe1;
}


.timeline ul li div.Cartoon-container {
  position: relative;
  bottom: 0;
  width: 350px;
  height: 145px;
  padding: 15px;
  background: #fedfe1;
}

.timeline ul li div.Cartoon-container::before {
  content: '';
  position: absolute;
  bottom: 20px;
  width: 0;
  height: 0;
  border-style: solid;
}

.timeline ul li:nth-child(odd) div.Cartoon-container {
  left: 45px;
}

.timeline ul li:nth-child(odd) div.Cartoon-container::before {
  left: -15px;
  border-width: 8px 16px 8px 0;
  border-color: transparent #fedfe1 transparent transparent;
}

.timeline ul li:nth-child(even) div.Cartoon-container {
  left: -419px;
}

.timeline ul li:nth-child(even) div.Cartoon-container::before {
  right: -15px;
  border-width: 8px 0 8px 16px;
  border-color: transparent transparent transparent #fedfe1;
}

.timeline ul li div time {
  display: block;
}

.timeline ul li div img {
  width: 100px;
}

.timeline ul li div .Cartoon-info {
  height: 142px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.timeline ul li:nth-child(odd) div time {
  position: relative;
  display: inline;
  left: -75px;
  bottom: 20px;
  z-index: 100;
}

.timeline ul li:nth-child(even) div time {
  position: relative;
  display: inline;
  left: 390px;
  bottom: 20px;
  z-index: 100;
}


